<template>
  <v-container>
    <v-row justify="center">
      <v-col
        cols="12"
        xl="8"
      >
        <v-sheet
          min-height="80vh"
          rounded="lg"
          class="pa-8 pa-sm-10"
        >
          <h1 class="font-weight-bold"></h1>
          <v-form class="pa-6" @submit.prevent="submit" ref="form">
            <v-row>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="title"
                  :rules="titleRules"
                  :counter="15"
                  label=""
                  color="white"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="proposer"
                  :rules="proposerRules"
                  label=""
                  color="white"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-text-field
              v-model="subtitle"
              :rules="subtitleRules"
              :counter="50"
              label=""
              color="white"
            ></v-text-field>
            <v-row>
              <v-col cols="12" md="6">
                <v-text-field
                  v-model="targetAmount"
                  :rules="targetAmountRules"
                  label=""
                  color="white"
                  outlined
                  append-icon="mdi-currency-twd"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-file-input
                  type="file"
                  accept="image/*"
                  v-model="image"
                  :rules="imageRules"
                  show-size
                  counter
                  label=""
                  color="white"
                ></v-file-input>
              </v-col>
            </v-row>
            <v-textarea
              v-model="description"
              :rules="descriptionRules"
              :counter="300"
              label=""
              color="white"
              class="mb-4"
            ></v-textarea>
            <!-- date picker -->
            <v-row class="mb-6">
              <v-col
                cols="12"
                md="6"
              >
                <v-date-picker
                  v-model="dates"
                  range
                  color="blue-grey"
                  :first-day-of-week="0"
                  locale="zh-cn"
                  :landscape="true"
                  min="2020-01-20"
                ></v-date-picker>
              </v-col>
              <v-col
                cols="12"
                md="6"
              >
                <v-text-field
                  :value="dateRangeText"
                  label=""
                  prepend-icon="mdi-calendar"
                  readonly
                ></v-text-field>
              </v-col>
            </v-row>
          <v-btn
            type="submit"
            class="mr-4 black--text"
            color="amber"
          >
            
          </v-btn>
          <v-btn text to="/">
            
          </v-btn>
          </v-form>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'Add',
  data: () => ({
    title: '',
    subtitle: '',
    proposer: '',
    targetAmount: '',
    image: [],
    description: '',
    // 
    titleRules: [
      v => !!v || '',
      v => v.length >= 4 || ' 4 ',
      v => v.length <= 15 || ' 15 '
    ],
    proposerRules: [
      v => !!v || ''
    ],
    subtitleRules: [
      v => !!v || '',
      v => v.length <= 50 || ' 50 '
    ],
    targetAmountRules: [
      v => !!v || '',
      v => !isNaN(v) || ''
    ],
    imageRules: [
      v => !!v || ''
    ],
    descriptionRules: [
      v => !!v || '',
      v => v.length <= 300 || ' 300 '
    ],
    // 
    dates: [new Date().toISOString().substr(0, 10), new Date().toISOString().substr(0, 10)]
  }),

  computed: {
    dateRangeText () {
      return this.dates.join(' ~ ')
    }
  },

  methods: {
    submit () {
      if (!this.$refs.form.validate()) return

      if (this.image.size > 1024 * 1024) {
        this.$swal({
          icon: 'warning',
          iconColor: '#FFC107',
          title: ' 1MB',
          confirmButtonColor: '#607D8B',
          confirmButtonText: ''
        })
      } else if (!this.image.type.includes('image')) {
        this.$swal({
          icon: 'warning',
          iconColor: '#FFC107',
          title: '',
          confirmButtonColor: '#607D8B',
          confirmButtonText: ''
        })
      } else {
        const formData = new FormData()
        formData.append('image', this.image)
        formData.append('title', this.title)
        formData.append('subtitle', this.subtitle)
        formData.append('proposer', this.proposer)
        formData.append('targetAmount', this.targetAmount)
        formData.append('description', this.description)
        formData.append('begin', this.dates[0])
        formData.append('finish', this.dates[1])

        this.axios.post(process.env.VUE_APP_API + '/projects/add', formData)
          .then(res => {
            if (res.data.success) {
              this.$swal({
                icon: 'success',
                iconColor: '#FFC107',
                title: '',
                confirmButtonColor: '#607D8B',
                confirmButtonText: ''
              })
              // 
              this.$refs.form.reset()
              // 
              this.$router.push('/')
            } else {
              this.$swal({
                icon: 'error',
                title: res.data.message,
                confirmButtonColor: '#607D8B',
                confirmButtonText: ''
              })
            }
          })
          .catch(err => {
            this.$swal({
              icon: 'error',
              title: err.response.data.message,
              confirmButtonColor: '#607D8B',
              confirmButtonText: ''
            })
          })
      }
    }
  }
}
</script>
